@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("https://fonts.gstatic.com/s/opensans/v18/mem8YaGs126MiZpBA-UFVZ0b.woff2")
    format("woff2");
}
.container {
  background-color: #001122;
  background-image: url("../img/Stars.png");
  background-size: cover; /* Adjusts the image size to cover the entire background */
  background-repeat: no-repeat;
  margin: 0;
  padding: 0;
}
.wrapper {
  height: 100vh;
  overflow-y: auto;
  overflow-x: hidden;
  perspective: 20px;
}

header {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  transform-style: preserve-3d;
}

.background {
  transform: translateZ(-18px) scale(1.7);
}

.foreground {
  transform: translateZ(-10px) scale(1.5);
}

.background,
.midground,
.foreground {
  position: absolute;
  height: 100%;
  width: 100%;
  object-fit: cover;
  z-index: -1;
}

.neonText {
  font-weight: 600;
  font-size: 6rem;
  background-color: #001122;
  border-radius: 4px;
  color: #ffffff;
  padding: 0.5rem 1rem;
  -webkit-box-shadow: 0px 0px 25px 10px rgba(0, 0, 0, 0.37);
  -moz-box-shadow: 0px 0px 25px 10px rgba(0, 0, 0, 0.37);
  box-shadow: 0px 0px 25px 10px rgba(0, 0, 0, 0.37);
  text-shadow: 2px 4px 7px rgba(0, 0, 0, 0.48);
  /* text-shadow: 0 0 1px #fff, 0 0 3px #fff, 0 0 1px #fff, 0 0 4px #ffd900d0,
    0 0 8px #ffd700d0, 0 0 9px #ffd700d0, 0 0 10px #ffd700d0, 0 0 15px #ffd700d0; */
}

section {
  z-index: -1;
  position: relative;

  padding-top: 30rem;
  background: rgb(204, 204, 204);
  background: #001122;
}

@media (max-width: 768px) {
  .neonText {
    font-size: 3rem;
  }
}
