.container {
  width: 100%;
  z-index: 1;
  background-color: #001122;

  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15rem 0;
}
.cart {
  width: 90rem;
  background-color: #ffd700;
  margin: 15rem 0;
  display: flex;
  border-radius: 4px;
}
.photo {
  width: 25rem;
  height: 25rem;
  background-color: #fff;
  display: flex;
  align-items: end;
  justify-content: center;
  border-top-left-radius: 1000px;
  border-top-right-radius: 1000px;
  margin: 1rem 1rem;
  border: 2px #222 solid;
  -webkit-box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
  -moz-box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
  box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
}
.photo > img {
  width: 19rem;
  height: 19rem;
}
.jpn {
  font-family: "Noto Sans JP", sans-serif;
  background-color: #001122;
  margin: 1rem 1rem 1rem 1rem;
  color: #fff;
  padding: 0.3rem;
  font-size: 48px;
  font-weight: 500;
  border-radius: 4px;
  width: 5rem;
  text-align: center;
  text-justify: center;
  -webkit-box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
  -moz-box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
  box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
}
.textArea {
  color: #fff;
  width: 60rem;
  margin: 1rem 1rem 1rem 1rem;
}
.name {
  background-color: #001122;
  padding: 0.3rem;
  margin-top: 2rem 0;
  width: fit-content;
  font-size: 36px;
  font-weight: 700;
  border-radius: 4px;
  -webkit-box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
  -moz-box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
  box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
}
.textDump {
  margin-top: 2rem;
  line-height: 1.5;
}
.passionate {
  background-color: #001122;
  padding: 0.4rem;
  width: fit-content;
  margin: 1rem 0;
  font-size: 20px;
  border-radius: 4px;
  -webkit-box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
  -moz-box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
  box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
}
.love {
  background-color: #001122;
  padding: 0.4rem;
  width: fit-content;
  margin: 2rem 0;
  font-size: 16px;
  border-radius: 4px;
  -webkit-box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
  -moz-box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
  box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
}
.pContainer {
  margin: 2rem 4rem 0 0;
}
.paragraph {
  display: inline;
  background-color: #001122;
  line-height: 1.7;
}

.scroll {
  display: flex;

  align-items: center;
  justify-content: end;
  gap: 1rem;
  margin-right: 1rem;
}
.scrollPragraph {
  background-color: #001122;
  padding: 0.2rem;
  width: fit-content;
  margin-top: 0.5rem;

  font-size: 12px;
  border-radius: 3px;
  -webkit-box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
  -moz-box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
  box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
}
.scrollButton {
}
.scrollButton > svg {
  width: 2rem;
  margin-top: 0.3rem;
  transition: all 0.3s;
  color: #fff;
}

@media only screen and (max-width: 1500px) {
  .cart {
    margin-top: 0;
    padding: 0;
    width: fit-content;
    flex-direction: column;
    align-items: center;
  }
  .jpn {
    width: fit-content;
  }
  .textArea {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .textDump {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .paragraph {
    text-align: center;
  }
}
@media only screen and (max-width: 1050px) {
  .cart {
    margin-top: 0;
    align-items: center;
    justify-content: center;
    width: 51rem;
  }
  .textArea {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .textDump {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 0;
    line-height: 1;
    width: 50rem;
  }
  .passionate {
    background-color: #001122;
    padding: 0.1rem;
    width: fit-content;
    margin: 0.5rem 0;
    font-size: 16px;
    border-radius: 2px;
    -webkit-box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
    -moz-box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
    box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
  }
  .love {
    background-color: #001122;
    padding: 0.1rem;
    width: fit-content;
    margin: 0.5rem 0;
    font-size: 14px;
    border-radius: 4px;
    -webkit-box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
    -moz-box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
  }
  .pContainer {
    margin-right: 0;
  }
}
@media only screen and (max-width: 860px) {
  .cart {
    margin-top: 0;
    width: 35rem;
  }
  .pContainer {
    width: 34rem;
  }
  .textDump {
    width: 32rem;
    text-align: center;
  }
}
@media only screen and (max-width: 590px) {
  .cart {
    width: 25rem;
    margin-top: 0;
  }
  .photo {
    width: 12rem;
    height: 12rem;
  }
  .photo > img {
    width: 9rem;
    height: 9rem;
  }

  .pContainer {
    width: 24rem;
  }
  .textDump {
    width: 22rem;
    text-align: center;
  }
}
@media only screen and (max-width: 435px) {
  .cart {
    width: 20rem;
  }
  .photo {
    width: 12rem;
    height: 12rem;
  }
  .photo > img {
    width: 9rem;
    height: 9rem;
  }

  .pContainer {
    width: 18rem;
  }
  .textDump {
    width: 19rem;
  }
}
