.container {
  margin: 3rem 3rem;
  padding: 1.5rem;
  border-radius: 5px;
  background-color: #001122;
}

.imageContainer {
  display: flex;
  justify-content: center;
}
.imageContainer > img {
  width: 50rem;
  border-radius: 4px;
}
a p {
  width: fit-content;
  text-decoration: none;
}
a {
  background-color: #ffd700;
  padding: 0.4rem;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.textContaioner {
  margin-top: 1rem;
  display: flex;
  justify-content: center;
}

.textContaioner > a > p {
  width: fit-content;
  color: #fff;
  background-color: #001122;
  padding: 0.3rem;
  border-radius: 4px;
}
a:hover {
  transition: all 0.3s;
  background-color: #001122;
}
@media only screen and (max-width: 1460px) {
  .imageContainer > img {
    width: 40rem;
  }
}
@media only screen and (max-width: 750px) {
  .imageContainer > img {
    width: 25rem;
  }
}
@media only screen and (max-width: 500px) {
  .imageContainer > img {
    width: 20rem;
  }
}
