.container {
  display: flex;
  align-items: center;
  justify-content: center;
}
.cart {
  background-color: #ffd700;
  border: 2px solid #000;
  width: 70rem;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  align-items: center;
  -webkit-box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
  -moz-box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
  box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
}
.title {
  color: #fff;
  font-size: 52px;
  font-weight: 700;
  background-color: #001122;
  margin-top: 5px;
  padding: 0.2rem;
  border-radius: 4px;
}

.more {
  background-color: #001122;
  margin-bottom: 1rem;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.imageContainer {
  display: flex;
  justify-content: center;
}
.textContainer {
  display: flex;
  justify-content: center;
}
.textContainer > p {
  font-size: 36px;
  font-weight: 600;
  color: #fff;
}
@media only screen and (max-width: 1460px) {
  .cart {
    width: 60rem;
  }
}
@media only screen and (max-width: 990px) {
  .cart {
    width: 45rem;
  }
}
@media only screen and (max-width: 750px) {
  .cart {
    width: 30rem;
  }
}
@media only screen and (max-width: 571px) {
  .cart {
    margin-bottom: 3rem;
  }
  .imageContainer {
    width: fit-content;
  }
  .imageContainer > img {
    width: 20rem;
  }
}
@media only screen and (max-width: 500px) {
  .cart {
    width: 24rem;
  }
}
