.container {
  margin: 8rem 8rem 0 8rem;
  padding-bottom: 3rem;
  display: flex;

  justify-content: space-between;
  gap: 2rem;

  background-color: #001122;
  color: #000;
  transition: all 0.3s;
}
.linkCart {
  background-color: #ffd700;
  height: fit-content;

  border-radius: 4px;
  display: flex;
  align-items: center;
  -webkit-box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
  -moz-box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
  box-shadow: 7px 10px 12px 3px rgba(0, 0, 0, 0.37);
}
.icon {
  width: 4rem;
  color: #fff;
  background-color: #001122;
  padding: 0.2rem;
  margin: 0.6rem;
  border-radius: 4px;
}
.iconn {
  width: 4rem;

  background-color: #001122;

  fill: #fff;
  padding: 0.2rem;
  margin: 0.6rem;
  border-radius: 4px;
}
a {
  text-decoration: none;
}
.linkCart:hover {
  background-color: #001122;
  transition: ease-out 0.3s;
}
@media only screen and (max-width: 570px) {
  .container {
    margin: 0 1rem;
    gap: 0.5rem;
  }
}
